.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
    transition: 0.5s ease;
}

.product:hover {
    transform: scale(1.9) ;
    -webkit-transform: scale(1.05) ;
    -moz-transform: scale(1.1) ;
    -webkit-box-shadow: 0px 0px 9px 0px #353535;
    box-shadow: 0px 0px 9px 0px #353535;
}

.product__rating {
    display: flex;
}

.product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    overflow:hidden;
}

.product > button:hover {
    -webkit-transform:scale(1.1);
    transform:scale(1.1);
}

.product__price {
    margin-top: 5px;
}

.product__info {
    height: 100px;
    margin-bottom: 15px;
}